@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400;600;700&display=swap);
/* Base */

*, ::before, ::after {
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}

:root{
  --secondary-color: #296EFA;
}

h1,h2,h3,ul,p {
  margin: 0;
}

h1,h2,h3 {
  font-weight: var(--font-semi-bold);
  color: var(--title-color);
}

ul {
  padding: 0;
  list-style-type: none;
}


a {
  text-decoration: none;
  color: var(--text-color);
}

img {
  max-width: 100%;
  height: auto;
}

body {
  margin: 0;
  font-family: 'Poppins', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.font-small {
  font-size: .60rem;
}

.cta {
  color: #296EFA;
  color: var(--secondary-color);
}


.container {
  padding: 2rem;
}

.wrapper {
  position: relative;
  z-index: 1;
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  max-width: 100vw;
  overflow: hidden;
}

.main-content {
  -webkit-flex: 3 1;
          flex: 3 1
}

.secondary-content {
  -webkit-flex: 1 1;
          flex: 1 1;
  background-color: #296EFA;
  text-align: center;
}

.secondary-content p {
  font-size: 0.55rem;
}

.circle {
  height: .29rem;
  width: .29rem;
  background-color: #AAAAAA;
  border-radius: 1rem;
}

.selected {
  background-color: #333333;
}
input, select {
    width: 100%;
    height: 100%;
    padding: .15rem;
    border-radius: .18rem;
    border: 1px solid #F6F6F6;
}

input:focus, select:focus {
    outline: none;
}

label {
    position: absolute;
    top: -22%;
    left: 5%;
    padding: .05rem;
    font-size: .5rem;
    background-color: white;

}

.field {
    width: 100%;
    height: 2rem;
    position: relative;
    padding: 0 0.2rem;
    color: #8a8a8a  ;
}

.field i {
    position: absolute;
    right: .55rem;
    top: 40%;
}

.error {
    color: red;
    transition: 0.5s;
}

.input-error {
    border: 1px solid red;
    transition: 0.5s;
}


